import React from "react"
import { Link } from "gatsby"
import SEO from "../components/seo"
import Breadcrumbs from "@material-ui/core/Breadcrumbs"
import Button from "@material-ui/core/Button"
import CodeIcon from "@material-ui/icons/Code"
import Grid from "@material-ui/core/Grid"
import "../site.css"

const MyMentorInfo = () => (
  <div style={{ display: "flex", flex: 1, justifyContent: "center" }}>
    <div className={"mymentor"}>
      <SEO
        title="Andrew Stolzle | MyMentor"
        description="mymentor mobile application for Android"
        lang="en"
      />
      <Breadcrumbs aria-label="mymentor breadcrumb">
        <Link
          to="/"
          style={{
            display: "flex",
            color: "#121212",
            fontFamily: `Sen, sans-serif`,
          }}
        >
          Home
        </Link>
        <div
          aria-current="page"
          style={{
            display: "flex",
            color: "#121212",
            fontFamily: `Sen, sans-serif`,
          }}
        >
          MyMentor
        </div>
      </Breadcrumbs>
      <span className={`index-title`}>MyMentor</span>
      <p style={{ fontWeight: 600 }}>
        Development Stack: Xamarin Forms, C#, XAML, Material UI, Telerik Kendo
        UI, SQL Server
        <br />
        <br />
        Other tools: Visual Studio, Android Emulator
      </p>
      <p>
        This was a project originally designed for my University computer
        science course. The goal of MyMentor is to provide a platform that
        teachers can use to help mentor students in both their inter- and
        extra-curricular needs. MyMentor aims to foster professional, yet
        personal relationships between students and teachers. These
        relationships will help students throughout their college journey and
        beyond by allowing them to easily communicate with peers and professors
        so that they have access to all of the support they need to succeed at
        their University.
      </p>
      <Grid container direction="row" justify="center">
        <figure>
          <img
            src={require("../images/mymentor/login.png")}
            alt={`mymentor login`}
            className={"mymentor-img"}
          />
          <figcaption>Login screen</figcaption>
        </figure>
      </Grid>
      <h3>Design and Structure</h3>
      <p>
        Two essential components guided the design of this product: an intuitive
        layout and consistent style across the app. MyMentor needed to have
        simple colors, bottom navigation, familiar buttons and icons.{" "}
        <em>
          I did not want to force the user to learn or adapt to new buttons,
          shapes, or actions
        </em>
        . In order to create simpler and more robust interfaces, I
        utilized the model-view-controller (MVC) pattern, a practice that is easy to
        implement with Xamarin. Here are two of the main screens a user will interact with:
      </p>
      <Grid container direction="row" justify="space-evenly">
        <figure>
          <img
            src={require("../images/mymentor/home.png")}
            alt={`mymentor home`}
            className={"mymentor-img"}
          />
          <figcaption>Student home page</figcaption>
        </figure>
        <figure>
          <img
            src={require("../images/mymentor/events.png")}
            alt={`mymentor events`}
            className={"mymentor-img"}
          />
          <figcaption>Community events and posts</figcaption>
        </figure>
      </Grid>
      <h3>Main Features</h3>
      <p>
        MyMentor revolves around matching students and professors primarily by
        their shared academic interests (i.e. matching users by subjects they
        both like). Upon initial log-in, users are asked to select their
        favorite subject, which MyMentor then uses to find the best matches for
        that person.
      </p>
      <Grid container direction="row" justify="center">
        <figure>
          <img
            src={require("../images/mymentor/interests.png")}
            alt={`mymentor profile`}
            className={"mymentor-img"}
          />
          <figcaption>Selecting favorite subject</figcaption>
        </figure>
      </Grid>
      <p>
        MyMentor allows users to communicate with each other with an instant
        message feature, which is accomplished via polling. Every 10 seconds,
        MyMentor polls a SQL database to check for any new messages for the
        logged-in user. Implementing push notifications would be an upgrade
        because with this method, as new messages would be delivered <em>only</em> when
        a new message is sent to the user. This prevents unecessary polling and
        database calls that the current method of polling relies on.
      </p>
      <Grid container direction="row" justify="center">
        <figure>
          <img
            src={require("../images/mymentor/chat.png")}
            alt={`mymentor chat`}
            className={"mymentor-img"}
          />
          <figcaption>Chat page</figcaption>
        </figure>
      </Grid>
      <p>
        Users can customize their profile by selecting which subjects they're
        interested in and by updating their display name. Profiles are visible
        to other users so that they can see what you are interested in and can then
        determine if you would be a good match for a mentorship. Users can also
        add new subjects to their profile after creating an account.
      </p>
      <Grid container direction="row" justify="space-evenly">
        <figure>
          <img
            src={require("../images/mymentor/profile.png")}
            alt={`mymentor profile`}
            className={"mymentor-img"}
          />
          <figcaption>User profile</figcaption>
        </figure>
        <figure>
          <img
            src={require("../images/mymentor/topic.png")}
            alt={`mymentor profile topic`}
            className={"mymentor-img"}
          />
          <figcaption>Adding subjects to profile</figcaption>
        </figure>
      </Grid>
      <Button
        color="primary"
        variant="contained"
        startIcon={<CodeIcon size="medium" />}
        onClick={() => window.open("https://github.com/frederickbyte/MyMentor")}
        style={{ margin: "0 auto", width: "fit-content" }}
      >
        SOURCE CODE
      </Button>
    </div>
  </div>
)

export default MyMentorInfo
